.StudentEntryContainer {
    width: 100%;
    font-family: Poppins;
}

.StudentEntryContainer .upper {
    width: 100%;
    padding: 0 30px;
    margin-bottom: 50px;
}

.StudentEntryContainer .upper h2 {
    font-weight: bolder;
}

.StudentEntryContainer .upper p {
    color: #aca6ac;
}

.StudentEntryContainer .FormContainer {
    width: 100%;
    padding: 20px;
    background-color: #fff;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.StudentEntryContainer .FormContainer .Section {
    width: 100%;
    display: grid;
    grid-template-columns: 25fr 75fr;
    grid-column-gap: 20px;
}

.StudentEntryContainer .FormContainer .Section .FieldsContainer {
    display: grid;
    grid-template-columns: 33.33fr 33.33fr 33.33fr;
    grid-gap: 20px;
}

.StudentEntryContainer .FormContainer .Section .FieldsContainer .Field {
    width: 100%;
    background-color: rgb(242, 244, 248, .6);
    border-bottom: 2px solid #b7c1ae;
    position: relative;
    z-index: 1;
}

.StudentEntryContainer .FormContainer .Section .FieldsContainer .Field label {
    z-index: -1;
    position: absolute;
    color: #b7c1ae;
    padding-left: 10px;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
    transition: .5s;
}

.StudentEntryContainer .FormContainer .Section .FieldsContainer .Field label.focusedLabel {
    position: absolute;
    color: #b7c1ae;
    padding-left: 10px;
    height: max-content;
    width: 100%;
    font-size: 10px;
    top: -8px;
    font-weight: bold;
}

.StudentEntryContainer .FormContainer .Section .FieldsContainer .Field input {
    width: 100%;
    height: 40px;
    background: none;
    outline: none;
    border: 0;
    padding: 0 10px;
    color: #72676e;
    z-index: 1;
}